@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.appContainer{position:fixed;left:0;top:0;width:100%;height:100%;z-index:2;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.appContainer .wordContainer{width:600px;position:relative;text-align:center;height:300px;-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column;border-radius:10px;box-shadow:0 1px 20px 1px rgba(0,0,0,0.1);-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;transition:opacity 0.2s ease-in-out, margin 0.15s ease-in-out}.appContainer .wordContainer .title{font-size:75px;margin-top:50px;transition:margin 0.3s ease-in-out, visibility 0.5s ease-in-out}.appContainer .wordContainer .meaning{margin-top:25px;visibility:hidden}.appContainer .wordContainer.expand .title{margin-top:0px}.appContainer .wordContainer.expand .meaning{visibility:visible}.appContainer .wordContainer.alignRight{margin-left:100%;opacity:0}.appContainer .wordContainer.alignLeft{margin-left:-100%;opacity:0}@media (max-width: 640px){.wordContainer{max-width:95%;height:500px}}

